<template>
     <div id="home">
          <nav>
               <div id="tabs">
                    <router-link id="landing_container" to="/">
                         <img src="../assets/logo.svg" id="logo" />
                    </router-link>

                    <div id="span_container">
                         <router-link to="/home/"><span id="a" v-on:click="changeSection(0)">Home</span></router-link>
                         <router-link to="/home/explore"><span id="b" v-on:click="changeSection(1)">Explore</span></router-link>
                         <router-link to="/home/account"><span id="c" v-on:click="changeSection(2)">Account</span></router-link>
                         <router-link to="/home/listing"><span id="d" v-on:click="changeSection(3)">Your Listing</span></router-link>
                         <router-link to="/home/lease"><span id="e" v-on:click="changeSection(4)">Your Lease</span></router-link>
                         <svg id="line">
                              <line class="cls-1" x1="17" y1="2" x2="65" y2="2" />
                         </svg>
                    </div>

                    <div id="pfp_container">
                         <img src="../assets/search.svg">
                         <input id="search" />
                         <Pfp></Pfp>
                    </div>
               </div>
          </nav>

          <div id="view">
               <router-view></router-view>
          </div>
     </div>
</template>

<script>
import Pfp from "../components/Pfp.vue";
import Dynamic from "./Dynamic.vue";
import Explore from "./Explore.vue";
import Listing from "./Listing.vue";
import Lease from "./Lease.vue";
import Account from "./Account.vue";
export default {
     name: "Home",
     data() {
          return {
               section: 0
          };
     },
     methods: {
          changeSection(n) {
               switch (this.section) {
                    case 0:
                         $("#a").removeClass("active");
                         break;
                    case 1:
                         $("#b").removeClass("active");
                         break;
                    case 2:
                         $("#c").removeClass("active");
                         break;
                    case 3:
                         $("#d").removeClass("active");
                         break;
                    case 4:
                         $("#e").removeClass("active");
                         break;
                    case 5:
                         $("#f").removeClass("active");
                         break;
                    case 6:
                         $("#g").removeClass("active");
                         break;
               }
               switch (n) {
                    case 0:
                         $("#a").addClass("active");
                         anime({
                              targets: "#line",
                              left: "0%",
                              duration: 100,
                              easing: "easeOutCubic"
                         });
                         anime({
                              targets: ".cls-1",
                              x2: [
                                   {
                                        duration: 50,
                                        value: "+=0"
                                   },
                                   {}
                              ],
                              duration: 100,
                              easing: "easeOutCubic"
                         });
                         break;
                    case 1:
                         $("#b").addClass("active");
                         anime({
                              targets: "#line",
                              left: "12.3%",
                              duration: 100,
                              easing: "easeOutCubic"
                         });
                         break;
                    case 2:
                         $("#c").addClass("active");
                         break;
                    case 3:
                         $("#d").addClass("active");
                         break;
                    case 4:
                         $("#e").addClass("active");
                         break;
                    case 5:
                         $("#f").addClass("active");
                         $("#line").css("grid-column", 7);
                         break;
                    case 6:
                         $("#g").addClass("active");
                         $("#line").css("grid-column", 8);
                         break;
               }
               this.section = n;
          }
     },
     computed: {
          width: function() {
               let x = window.innerWidth * 0.5;
               console.log(x);
               return x;
          }
     },
     mounted() {
          this.changeSection(0);
     },
     components: {
          Pfp,
          Account,
          Dynamic,
          Explore,
          Listing,
          Lease
     }
};
</script>

